import React from 'react';
import '../App.css';
import logo from "../logos/milkyway.png";
import { Box, SimpleGrid, Image, Flex, Text, Spacer, HStack, Button, Heading, Center } from '@chakra-ui/react';

import Web3 from 'web3';
import { isEmptyObject, connectWeb3, disconnectWeb3 } from './utils/connectWeb3';

class Navbar extends React.Component {

	connectweb3 = async () => {
		const { web3Modal, updateWeb3, toast } = this.props;

		try {
			const provider = await web3Modal.connect();

			if (provider.chainId !== "0x30da5") { // The Chain ID(s) that are supported
				return toast({
					title: 'Wallet Error',
					description: 'Wrong Network, please connect to Milkomeda',
					status: 'error',
					duration: 6000,
					isClosable: true,
				});
			}

			const web3 = new Web3(provider);

			updateWeb3(web3);

			toast({
				title: 'Connected!',
				description: 'Successfully connected to Milkomeda',
				status: 'success',
				duration: 6000,
				isClosable: true,
			})
		} catch {
			toast({
				title: 'Wallet Error',
				description: 'Error when trying to connect to wallet.',
				status: 'error',
				duration: 6000,
				isClosable: true,
			})
		}
	}

	render() {
		const { web3Modal, updateWeb3, toast, web3 } = this.props;

		return (
			<Box className="navbar" height='8vh'>
				<Flex align="center" justify="space-between" mb={8} mt="-2vw">
					<HStack spacing="2vw">
						<Image className="ImageClass" src={logo} alt="Milkyway" boxSize="4vw" />
						<Heading size="md">Lending</Heading>
					</HStack>
					<Spacer />
					<HStack spacing="1vw">
						<Heading size="sm">{isEmptyObject(web3) ? "Not Connected" : "Milkomeda"}</Heading>
						<Heading size="sm">EN</Heading>
						<Button size="md" onClick={() => isEmptyObject(web3) ? connectWeb3(toast, web3Modal, updateWeb3) : disconnectWeb3(updateWeb3, toast)} className="ConnectWeb3">{isEmptyObject(web3) ? "Connect" : "Disconnect"}</Button>
					</HStack>
				</Flex>
			</Box>
		)
	}
}

export default Navbar;